
// initial state
const state = {
  // This is where GeoMet Layers Stuff Start
  addedGeoMetLayers: [],
  addedGeoMetLayersFlag: false,
  selectedAddedGeometLayers: []
}

// getters
const getters = {
  // This is where GeoMet Layers Stuff Start
  addGeoMetLayerOverlay: (state) => {
    return state.addedGeoMetLayersFlag
  },
  getAddedGeoMetLayers: (state) => {
    return state.addedGeoMetLayers
  },
  getSelectedAddedGeometLayers: (state) => {
    return state.selectedAddedGeometLayers
  }
}

// mutations
const mutations = {
  // This is where GeoMet Layers Stuff Start
  changeGeoMetAddOverlayFlag (state, payload) {
    state.addedGeoMetLayersFlag = payload
  },
  addGeoMetLayer (state, payload) {
    state.addedGeoMetLayers.push(payload)
  },
  setSelectedAddedGeometLayers (state, payload) {
    state.selectedAddedGeometLayers = payload
  }
}
// actions
const actions = {
  // This is where GeoMet Layers Stuff Start
  updateGeoMetAddFlag ({ commit }, payload) {
    commit('changeGeoMetAddOverlayFlag', payload)
  },
  addGeoMetLayer ({ commit }, payload) {
    commit('addGeoMetLayer', payload)
  },
  updateSelectedAddedGeometLayers ({ commit }, payload) {
    commit('setSelectedAddedGeometLayers', payload)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
