<template>
    <v-container>
        <!-- Search Field -->
        <v-row v-if="listFlag" justify="center" align="center" style="border-color: red;">
                <v-text-field
                    v-model="searchGeoMet"
                    color="white"
                    class="my-2"
                    label="Search GeoMet-Weather Layers"
                    rounded
                    filled
                    hide-details>
                </v-text-field>
        </v-row>
        <!-- Loading GeoMet Layers -->
        <v-row justify="center" v-if="!listFlag">
            <v-card loading>
                <v-card-title>
                    Loading GeoMet-Weather Layers
                </v-card-title>
            </v-card>
        </v-row>
        <!-- TreeView -->
        <v-row>
            <v-expansion-panels v-model="panel">
                <v-expansion-panel v-model="panel">
                <v-expansion-panel-header>
                    GeoMet-Weather Layer Tree
                </v-expansion-panel-header>
                <v-expansion-panel-content>
                    <v-sheet>
                        <v-treeview
                            :items="filterObj"
                            :open.sync="open"
                            item-key="Title"
                            dense
                            class="overflow-y-auto overflow-x-auto"
                            style="max-height: 700px; min-width: 500px;"
                            ref="tree">
                            <template v-slot:prepend="{ item }">
                                <v-row align="center">
                                <v-col cols="auto">
                                    <h3 v-if="item.children" class="text-no-wrap">{{ item.Title }}</h3>
                                    <v-tooltip v-if="!item.children" right color="light-blue" nudge-right=45>
                                        <template v-slot:activator="{ on, attrs }">
                                            <v-row v-bind="attrs" v-on="on">
                                                <v-col>
                                                    <h4 v-if="!item.children" class="text-no-wrap">{{ item.Title }}</h4>
                                                </v-col>
                                                <v-col>
                                                    <h4 v-if="!item.children" class="text-no-wrap grey--text">({{ item.Name }})</h4>
                                                </v-col>
                                            </v-row>
                                        </template>
                                        <span>
                                            <h4 v-if="!item.children" style="max-width: 600px;">{{ item.Abstract }}</h4>
                                        </span>
                                    </v-tooltip>
                                    
                                    <v-divider v-if="!item.children"></v-divider>
                                    <h5 v-if="!item.children" style="max-width: 600px;">{{ item.Dimension }}</h5>
                                </v-col>
                                <v-col cols="auto" v-if="!item.children">
                                    <v-btn x-large color="white" icon @click="addLayerEvent(item)" :disabled="clicked.includes(item.Name)">
                                    <v-icon color="primary">
                                        mdi-plus
                                    </v-icon>
                                    </v-btn>
                                </v-col>
                                </v-row>
                            </template>
                        </v-treeview>
                    </v-sheet>
                </v-expansion-panel-content>
                </v-expansion-panel>
            </v-expansion-panels>   
        </v-row>
        <!-- Map -->
        <v-row >
            <div ref="map" class="map" style="width: 100%; height: 500px;"></div>
        </v-row>
        <!-- Controls for added and selected layers -->
        <!-- <v-row style="padding-bottom: 150px;">
            <v-expansion-panels multiple>
                <v-expansion-panel 
                    v-for="(ls,j) in addedGeoMetLayers"
                    :key="j">
                    <v-expansion-panel-header>
                        {{ ls.get('layerTitle') }} : {{ opacities.find(l => {return l.layerName === ls.get('layerName')}).layerCurrentTime }}
                    </v-expansion-panel-header>
                    <v-expansion-panel-content>
                        <v-row align="center">
                            <v-col cols="8">
                                <v-row align="center">
                                    <v-col cols="1">
                                        <v-btn icon x-large outlined style="margin: 0.5rem;" color="primary" @click="chipClickEvent(ls)">
                                            <v-icon dark>
                                                {{ ls.getVisible() ? 'mdi-eye-off' : 'mdi-eye' }}
                                            </v-icon>
                                        </v-btn>
                                    </v-col>
                                    <v-col cols="8" align-self="center">
                                       <v-slider 
                                            v-model="opacities.find(l => {return l.layerName === ls.get('layerName')}).layerOpacity"  
                                            min="0" 
                                            max="1" 
                                            step="0.05"
                                            @input="setOpacityGeneric(ls)">
                                            <template #prepend>
                                                Opacity
                                            </template>
                                            <template #append>
                                                {{ Math.ceil(Math.round(ls.getOpacity() * 100)) }}%
                                            </template>
                                        </v-slider> 
                                    </v-col>
                                    <v-col cols="1">
                                       <v-btn icon x-large outlined style="margin: 0.5rem;" color="primary" @click="sonOfMapToGIF(ls)">
                                            <v-icon dark>
                                                mdi-file-gif-box
                                            </v-icon>
                                        </v-btn> 
                                    </v-col>
                                </v-row>
                            </v-col>
                            <v-col cols="4">
                                <v-expansion-panels multiple>
                                    <v-expansion-panel>
                                        <v-expansion-panel-header>
                                            Legend for {{ ls.get('layerTitle') }}
                                        </v-expansion-panel-header>
                                        <v-expansion-panel-content>
                                            <v-img 
                                                :src="`https://geo.weather.gc.ca/geomet?version=1.3.0&service=WMS&request=GetLegendGraphic&sld_version=1.1.0&layer=${ls.get('layerName')}&format=image/png`"
                                                max-height="300"
                                                max-width="200"
                                                style="margin: auto;"
                                                contain></v-img>
                                        </v-expansion-panel-content>
                                    </v-expansion-panel>
                                </v-expansion-panels>      
                            </v-col>
                        </v-row>
                    </v-expansion-panel-content>
                </v-expansion-panel>
            </v-expansion-panels>
        </v-row> -->
        <v-row style="padding-bottom: 50px;">
            <v-card v-for="(ls,j) in addedGeoMetLayers" :key="j" outlined elevation="2" class="mt-4" width="100%">
                <v-card-title>
                    {{ ls.get('layerTitle') }} : {{ opacities.find(l => {return l.layerName === ls.get('layerName')}).layerCurrentTime }}
                </v-card-title>
                <v-card-text>
                    <v-row align="center">
                        <v-col cols="8">
                            <v-row align="center">
                                <v-col cols="1">
                                    <v-btn icon large outlined style="margin: 0.5rem;" color="primary" @click="chipClickEvent(ls)">
                                        <v-icon dark>
                                            {{ ls.getVisible() ? 'mdi-eye-off' : 'mdi-eye' }}
                                        </v-icon>
                                    </v-btn>
                                </v-col>
                                <v-col cols="8" align-self="center">
                                    <v-slider 
                                        v-model="opacities.find(l => {return l.layerName === ls.get('layerName')}).layerOpacity"  
                                        min="0" 
                                        max="1" 
                                        step="0.05"
                                        dense
                                        @input="setOpacityGeneric(ls)">
                                        <template #prepend>
                                            Opacity
                                        </template>
                                        <template #append>
                                            {{ Math.ceil(Math.round(ls.getOpacity() * 100)) }}%
                                        </template>
                                    </v-slider> 
                                </v-col>
                                <v-col cols="1">
                                    <v-text-field 
                                        label="Z Index" 
                                        value="ls.getZIndex()"
                                        v-model="opacities.find(l => {return l.layerName === ls.get('layerName')}).layerZIndex"
                                        type="number"
                                        @input="zIndexEvent(ls)">
                                    </v-text-field>
                                </v-col>
                            </v-row>
                        </v-col>
                        <v-col cols="4">
                            <v-expansion-panels multiple>
                                <v-expansion-panel>
                                    <v-expansion-panel-header>
                                        Legend for {{ ls.get('layerTitle') }}
                                    </v-expansion-panel-header>
                                    <v-expansion-panel-content>
                                        <v-img 
                                            :src="`https://geo.weather.gc.ca/geomet?version=1.3.0&service=WMS&request=GetLegendGraphic&sld_version=1.1.0&layer=${ls.get('layerName')}&format=image/png`"
                                            max-height="300"
                                            max-width="200"
                                            style="margin: auto;"
                                            contain></v-img>
                                    </v-expansion-panel-content>
                                </v-expansion-panel>
                            </v-expansion-panels>      
                        </v-col>
                    </v-row>
                </v-card-text>
            </v-card>
        </v-row>

        <!-- GIF progress bar -->
        <v-row v-if="this.gifstarted">
            <v-progress-linear
                v-model="this.gifProgressPercent"
                height="20"
                class="my-4"
                rounded
                >
                <strong>{{ Math.ceil(this.gifProgressPercent) }}%</strong>
            </v-progress-linear>
        </v-row>

        <!-- Download GIF and Related Controls -->
        <v-row v-if="gifDoneFlag" style="padding-bottom: 50px;">
            <v-expansion-panels v-model="gifExportPanel">
                <v-expansion-panel v-model="gifExportPanel">
                    <v-expansion-panel-header>
                        GIF Created
                    </v-expansion-panel-header>
                    <v-expansion-panel-content align="center">
                        <img id="gifimg"/>
                        <v-img :src="gifURL"></v-img>
                        <v-btn icon x-large outlined style="margin: 0.5rem;" color="primary" @click="downloadGIF()">
                            <v-icon dark>
                                mdi-download
                            </v-icon>
                        </v-btn>
                    </v-expansion-panel-content>
                </v-expansion-panel>
            </v-expansion-panels>
        </v-row>
        <!-- Extra controls row -->
        <v-row v-if="addedGeoMetLayers.length != 0" style="padding-bottom: 150px;">
            <v-col>
                <v-select
                    v-model="gifDriverLayer"
                    :items="addedGeoMetLayers"
                    label="Select the GIF's Driving Layer"
                    return-object
                    single-line
                    >
                    <template v-slot:item="{item}">
                        {{item.get('layerTitle')}}
                    </template>    
                    <template v-slot:selection="{item}">
                        {{item.get('layerTitle')}}
                    </template>
                </v-select>
            </v-col>
            <v-col cols="1">
                <v-switch
                    v-model="frameTimeText"
                    label="Frame Time Text"
                ></v-switch>
            </v-col>
            <v-col cols="1">
                <v-switch
                    v-model="frameTitleText"
                    label="Frame Title Text"
                ></v-switch>
            </v-col>
            <v-col>
                <v-slider
                    v-model="delayInput"
                    min="10"
                    max="3000"
                    step="10"
                    thumb-label="always">
                </v-slider>
            </v-col>
            <v-col cols="1">
                <v-btn icon x-large outlined style="margin: 0.5rem;" color="primary" @click="sonOfMapToGIF(gifDriverLayer)">
                    <v-icon dark>
                        mdi-file-gif-box
                    </v-icon>
                </v-btn> 
            </v-col>
        </v-row>

        <a id="image-download" download="firework_experiment.png"></a>
        <a id="gif-download" download="gif_export.gif"></a>
    </v-container>
</template>

<script>
import Map from 'ol/Map';
import {defaults as defaultControls} from 'ol/control';
import View from 'ol/View';
import TileLayer from 'ol/layer/Tile';
import OSM from 'ol/source/OSM';
import Image from 'ol/layer/Image';
import ImageWMS from 'ol/source/ImageWMS';
import {fromLonLat} from 'ol/proj';
import GIF from 'gif.js';
import 'ol/ol.css'
import axios from 'axios'
import SaxonJS from 'saxon-js'

export default {
    async mounted() {
        this.getCapabilitiesGeoMet();

        this.map = new Map({
            target: this.$refs['map'],
            layers: [
                this.osm,
            ],
            view: new View({
                center: fromLonLat([-90, 55]),
                zoom: 4
            }),
            controls: defaultControls({attribution: false})
        });
    },
    computed: {
        gifProgressPercent () {
            return Math.round((this.gifprogress / (this.giflength)) * 100)
        },
        listFlag () {
            return this.treeItems.length !== 0
        },
        filterObj () {
            return this.filterObjectFunction(this.treeItems, ({ Title }) => Title.toLowerCase().includes(this.searchGeoMet.toLowerCase()))
        },
        clicked () {
            return this.addedGeoMetLayers.map(o => o.get('layerName'))
        },
        getZIndexAbstract (layer) {
            return layer.getZIndex();
        }
    },
    methods: {
        downloadGIF: function() {
            var giflink = document.getElementById('gif-download');
            giflink.href = this.gifURL;
            giflink.click();
        },
        async addLayerEvent(layer) {
            const data = this.getStartEndTimeNew(layer.Dimension);
            // const data = await this.getStartEndTime(layer.Name);
            let tempLayer = new Image({
                                            source: new ImageWMS({
                                                format: "image/png",
                                                url: "https://geo.weather.gc.ca/geomet/",
                                                params: { LAYERS: layer.Name},
                                                transition: 0,
                                                crossOrigin: "Anonymous"
                                                }),
                                            visible: true,
                                            opacity: 0.7,
                                            zIndex: 10
                                        })
            tempLayer.setProperties({   'layerName': layer.Name,
                                        'layerTitle': layer.Title,
                                        'layerStartTime': data[0],
                                        'layerEndTime': data[1],
                                        'layerTimeStep': this.convertTimeStepToInt(data[2])})
            
            var tempDateArray = await this.getDateArray(data[0],data[1],data[2]);
            this.opacities.push({   'layerName': layer.Name,
                                    'layerAbstract': layer.Abstract,
                                    'layerOpacity': 0.7,
                                    'layerCurrentTime': data[0],
                                    'layerDateArray': tempDateArray,
                                    'layerDateArrayLength': tempDateArray.length, 
                                    'layerAnimationID': null,
                                    'layerSliderValue': 0,
                                    'layerZIndex': 10 })
            this.map.addLayer(tempLayer)
            this.addedGeoMetLayers.push(tempLayer);
            console.log(this.addedGeoMetLayers);
        },
        async setTimeDate(layer,dateToSet) {
            this.opacities.find(l => {return l.layerName === layer.get('layerName')})["layerCurrentTime"] = dateToSet;
            layer.getSource().updateParams({ TIME: dateToSet.toISOString().split(".")[0] + "Z"})
        },
        resetCurrentTime(layer) {
            this.opacities.find(l => {return l.layerName === layer.get('layerName')})["layerCurrentTime"] = layer.get('layerStartTime');
            var tempc = this.opacities.find(l => {return l.layerName === layer.get('layerName')})["layerCurrentTime"];
            layer.getSource().updateParams({ TIME: tempc.toISOString().split(".")[0] + "Z" });
        },
        async getDateArray(start,end,step) {
            var dateArray = new Array();
            var s = start;
            var e = end;
            var i = this.convertTimeStepToInt(step);
            while (s <= e) {
                dateArray.push(s);
                s = new Date( s.setMinutes( s.getMinutes() + i ) );
            }
            return dateArray;
        },
        getStartEndTimeNew (layerDimension) {
            var data = layerDimension.split("/");
            return [new Date(data[0]), new Date(data[1]), data[2]];             
        },
        setOpacityGeneric: function(layer) {
            layer.setOpacity(this.opacities.find(l => {return l.layerName === layer.get('layerName')})["layerOpacity"]);
        },
        zIndexEvent: function(layer) {
            layer.setZIndex(this.opacities.find(l => {return l.layerName === layer.get('layerName')})["layerZIndex"]);
        },
        async chipClickEvent(layer) { 
            layer.setVisible(!layer.getVisible());
        },
        // AddGeometLayer Functions
        filterObjectFunction: function (array, fn) {
            return array.reduce((r, o) => {
                var children = this.filterObjectFunction(o.children || [], fn)
                if (fn(o) || children.length) r.push(Object.assign({}, o, children.length && { children }))
                return r
            }, [])
        },
        async sonOfMapToGIF(layer) {
            var preGIFVisibleLayers = this.addedGeoMetLayers.filter(function (visiblemaybe) {
                return visiblemaybe.getVisible()
            });
            console.log(preGIFVisibleLayers);
            var tempDateArray = this.opacities.find(l => {return l.layerName === layer.get('layerName')})["layerDateArray"];
            var dal = this.opacities.find(l => {return l.layerName === layer.get('layerName')})["layerDateArrayLength"];
            var tn = layer.get('layerName')
            var tt = layer.get('layerTitle')
            this.giflength = dal;
            this.gifstarted = true;
            var tempgif = new GIF({
                workers: 4,
                quality: 20,
                height: this.map.getSize()[1],
                width: this.map.getSize()[0],
                // workerScript: 'gif.worker.js'
                workerScript: process.env.VUE_APP_PUBLIC_PATH_EN + 'gif.worker.js'
            });

            for (let i = 0 ; i < tempDateArray.length - 1 ; i++) {
                this.setTimeDate(layer,tempDateArray[i]);
                for (let j = 0 ; j < preGIFVisibleLayers.length - 1 ; j++) {
                    if (tn !== preGIFVisibleLayers[j].get('layerName')) {
                        var tda = this.opacities.find(la => {return la.layerName === preGIFVisibleLayers[j].get('layerName')})["layerDateArray"]
                        for (let k = 0 ; k < tda.length - 1 ; k++) {
                            if (tempDateArray[i].getTime() === tda[k].getTime()) {
                                this.setTimeDate(preGIFVisibleLayers[j],tda[k]);
                            }
                        }
                    }
                }
                await new Promise(resolve => this.map.once('rendercomplete', resolve));
                this.mapToGIFCallback(tempgif,tt,tempDateArray[i]);
                this.gifprogress++;
            }
            // Important to keep the arrow function in order to access context inside callback
            tempgif.on('finished', (blob) => {
                this.newestblob = blob;
                this.gifURL = URL.createObjectURL(this.newestblob);
            });
            tempgif.render();
            this.gifstarted = false;
            this.gifprogress = 0;
            this.giflength = 0;
            tempgif = null;
            this.resetCurrentTime(layer);
            this.gifDoneFlag = true;
        },
        mapToGIFCallback(gif,layerName,timeStep) {
            var tempFrameTitleText = this.frameTitleText;
            var tempFrameTimeText = this.frameTimeText;
            var roundRectTemp = this.roundRect;
            const mapCanvas = document.createElement('canvas');
            const divElement = document.querySelector(".map");
            mapCanvas.width = divElement.offsetWidth;//size[0];
            mapCanvas.height = divElement.offsetHeight;//size[1];
            const mapContext = mapCanvas.getContext('2d');
            Array.prototype.forEach.call(
                document.querySelectorAll('.ol-layer canvas'),
                function (canvas) {
                    if (canvas.width > 0) {
                        const opacity = canvas.parentNode.style.opacity;
                        mapContext.globalAlpha = opacity === '' ? 1 : Number(opacity);
                        const transform = canvas.style.transform;
                        const matrix = transform
                                                .match(/^matrix\(([^\(]*)\)$/)[1] //eslint-disable-line
                                                .split(',')
                                                .map(Number);
                        CanvasRenderingContext2D.prototype.setTransform.apply(mapContext,matrix);
                        mapContext.drawImage(canvas, 0, 0);
                    }
                }
            );
            if(tempFrameTitleText) {
                mapContext.strokeStyle = 'white';
                mapContext.fillStyle = 'white';
                mapContext.textAlign = 'center';
                mapContext.font = '24px Airal';
                var ftitleh = parseInt(mapContext.font.substring(0, 2));
                var ttiw = mapContext.measureText(layerName);
                const bx = (divElement.offsetWidth / 2) - 1.1 * (ttiw.width / 2);
                roundRectTemp(mapContext, bx, ftitleh / 6 , 1.1 * ttiw.width, 1.1 * ftitleh, { tl: 10, tr: 10, br: 10, bl: 10},true);
                mapContext.strokeStyle = 'black';
                mapContext.fillStyle = 'black';
                mapContext.fillText(layerName, divElement.offsetWidth / 2 , ftitleh);
            }

            if(tempFrameTimeText) {
                mapContext.strokeStyle = 'white';
                mapContext.fillStyle = 'white';
                mapContext.textAlign = 'center';
                mapContext.font = '24px Airal';
                var ftimeh = parseInt(mapContext.font.substring(0, 2));
                var ttw = mapContext.measureText(timeStep);
                roundRectTemp(mapContext, ftimeh / 2 , divElement.offsetHeight - ftimeh , 1.01 * ttw.width, 1.1 * ftimeh, { tl: 10, tr: 10, br: 10, bl: 10},true);
                mapContext.strokeStyle = 'black';
                mapContext.fillStyle = 'black';
                mapContext.fillText(timeStep, ttw.width / 1.9 , divElement.offsetHeight - ftimeh / 6);
            }
            gif.addFrame(mapCanvas, {copy:true, delay: this.delayInput});
        },
        getCapabilitiesGeoMet: function () {
            axios.get('https://geo.weather.gc.ca/geomet?lang=en&service=WMS&version=1.3.0&request=GetCapabilities').then((response) => {
                const xslt =
                `<xsl:stylesheet 
                xmlns:xsl="http://www.w3.org/1999/XSL/Transform"
                xmlns:xs="http://www.w3.org/2001/XMLSchema"
                xmlns:mf="http://example.com/mf"
                exclude-result-prefixes="#all"
                xpath-default-namespace="http://www.opengis.net/wms"
                xmlns="http://www.w3.org/2005/xpath-functions"
                expand-text="yes"
                version="3.0">

                <xsl:strip-space elements="*"/>
                
                <xsl:mode on-no-match="shallow-skip"/>

                <xsl:output method="json" build-tree="no" indent="yes"/>
                
                <xsl:template match="/WMS_Capabilities/Capability/Layer" priority="5">
                    <xsl:map>
                    <xsl:apply-templates/>
                    </xsl:map>
                </xsl:template>
                
                <xsl:template match="Layer/Title[1] | Layer/Name[1] | Layer/Abstract[1] | Layer/Dimension[1]">
                    <xsl:map-entry key="local-name()" select="data()"/>
                </xsl:template>
                
                <xsl:template match="Layer[1]">
                    <xsl:map-entry key="'children'">
                    <xsl:sequence select="array { ../Layer/mf:apply-templates(.) }"/>
                    </xsl:map-entry>
                </xsl:template>
                
                <xsl:template match="Layer[position() > 1]"/>
                
                <xsl:function name="mf:apply-templates" as="item()*">
                    <xsl:param name="elements" as="element(*)*"/>
                    <xsl:map>
                    <xsl:apply-templates select="$elements/*"/>      
                    </xsl:map>
                </xsl:function>
                
                </xsl:stylesheet>`
                const jsonResult = SaxonJS.XPath.evaluate(`
                transform(
                    map { 
                    'source-node' : parse-xml($xml), 
                    'stylesheet-text' : $xslt, 
                    'delivery-format' : 'raw' 
                    }
                    )?output`,
                [],
                { 'params': { 'xml': response.data, 'xslt': xslt } }
                )
                this.treeItems = jsonResult.children
                this.geometRoot = jsonResult.Title
            })
        },
        convertTimeStepToInt: function(timestep) {
            var stepint;
            switch(timestep) {
                case "PT10M":
                    stepint = 10;
                    break;
                case "PT15M":
                    stepint = 15;
                    break;
                case "PT30M":
                    stepint = 10;
                    break;
                case "PT1H":
                    stepint = 60;
                    break;
                case "PT3H":
                    stepint = 180;
                    break;
                case "PT6H":
                    stepint = 360;
                    break;
                case "PT12H":
                    stepint = 720;
                    break;
                case "PT24H":
                    stepint = 1440;
                    break;
            }
            return stepint;
        },
        roundRect(ctx, x, y, width, height, radius, fill, stroke) {
            if (typeof stroke === 'undefined') {
                stroke = true;
            }
            if (typeof radius === 'undefined') {
                radius = 5;
            }
            if (typeof radius === 'number') {
                radius = {tl: radius, tr: radius, br: radius, bl: radius};
            } else {
                var defaultRadius = {tl: 0, tr: 0, br: 0, bl: 0};
                for (var side in defaultRadius) {
                radius[side] = radius[side] || defaultRadius[side];
                }
            }
            ctx.beginPath();
            ctx.moveTo(x + radius.tl, y);
            ctx.lineTo(x + width - radius.tr, y);
            ctx.quadraticCurveTo(x + width, y, x + width, y + radius.tr);
            ctx.lineTo(x + width, y + height - radius.br);
            ctx.quadraticCurveTo(x + width, y + height, x + width - radius.br, y + height);
            ctx.lineTo(x + radius.bl, y + height);
            ctx.quadraticCurveTo(x, y + height, x, y + height - radius.bl);
            ctx.lineTo(x, y + radius.tl);
            ctx.quadraticCurveTo(x, y, x + radius.tl, y);
            ctx.closePath();
            if (fill) { ctx.fill(); }
            if (stroke) { ctx.stroke(); }
        }
    },
    data () {
        return {
            map: null,
            gif: null,
            gifstarted: false,
